@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
* {
  box-sizing: border-box;
  font-family: "Noto Sans KR", sans-serif;
}

input {
  all: unset;
  box-sizing: border-box;
  -webkit-appearance: none;
          appearance: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  color: white;
  width: 100vw;
  height: 100vh;
}

#root {
  width: 100%;
  height: 100%;
}
.App {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center center;
  position: relative;
}

button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

form {
  width: 100%;
}

.logo {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  width: 150px;
  height: 150px;
}
#logo {
  width: 100%;
  height: 100%;
  background-size: 130% auto;
  background-repeat: no-repeat;
}

.weather_box {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 10;
  width: 10%;
  height: 80px;
}

.weather_icon_box {
  width: 100%;
  height: 50px;
}

.weather_icon {
  display: block;
  width: auto;
  height: 100%;
  margin: 0 auto;
}

.weatherText {
  line-height: 1.4rem;
  text-align: center;
  font-family: "Noto Sans KR", sans-serif;
}

.container {
  position: absolute;
  top: 10vh;
  left: 25vw;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 50vw;
  height: 70vh;
}

.inner__container {
  position: relative;
  width: 45%;
  height: 100%;
}

#btnCreate {
  display: block;
  width: 100%;
  height: 20px;
  text-align: right;
}

.phoneWrap {
  position: relative;
  width: 55%;
  height: 100%;
}

.phone {
  position: absolute;
  top: 0;
  left: 0px;
  width: 100%;
  height: 100%;
  background-size: auto 100%;
  background-repeat: no-repeat;
  background-position: center;
  z-index: 3;
}

.inner__phone {
  position: absolute;
  width: 59%;
  height: 100%;
  top: 0;
  left: 20%;
  background-color: #000;
  z-index: 1;
  background-image: url(/meoIb/static/media/meoib_logo.2f4e5b89.png);
  background-size: 80% auto;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 50px;
}

.contentWrap {
  width: 100%;
  height: calc(100% - 20px);
  overflow-y: scroll;
}

.contentWrap::-webkit-scrollbar {
}

.inner__contentWrap {
  width: 95%;
  height: auto;
}

.contentBox {
  background-color: #fff;
  color: #000;
  margin-bottom: 15px;
  padding: 10px;
  border-radius: 10px;
  width: 100%;
  position: relative;
  font-family: "Noto Sans KR", sans-serif;
}

.formBtnBox {
  float: right;
}

.formBtn {
  border-radius: 4px;
  margin-right: 5px;
  border: none;
}
.formBtn:hover {
  font-weight: bold;
  cursor: pointer;
}

.content__owner {
  font-weight: bold;
}

.content__text {
  padding: 5px;
  line-height: 2rem;
  font-family: "Noto Sans KR", sans-serif;
}

.content__time {
  text-align: right;
  font-size: 0.8rem;
  font-family: "Noto Sans KR", sans-serif;
}

.bgCover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(40, 40, 40, 0.5);
  z-index: 100;
}

.boardBox {
  width: 30vw;
  height: 36vh;
  border-radius: 10px;
  background-color: #fff;
  margin-top: 20vh;
  margin-left: 35vw;
}

.board {
  display: block;
  width: 100%;
  height: 100%;
  position: relative;
}

.boardCloseIcon {
  color: #333;
  font-size: 2rem;
  float: right;
  display: block;
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}

.boardTitle {
  font-size: 1.1rem;
  color: #333;
  font-weight: bold;
  padding: 10px 0 5px 15px;
  line-height: 2rem;
  font-family: "Noto Sans KR", sans-serif;
}

.boardInput {
  color: #333;
  font-size: 0.9rem;
  line-height: 1.5rem;
  margin-left: 15px;
  display: block;
  width: 90%;
  height: 60%;
  font-family: "Noto Sans KR", sans-serif;
}

.boardBox .formBtn {
  display: block;
  padding: 0 30px;
  height: 14%;
  background-color: #aaa;
  color: #333;
  margin: 0 auto;
}

.boardBox .formBtn:hover {
  padding: 0 25px;
  height: 13%;
  margin-top: 0.25%;
}

.reportBox {
  position: absolute;
  z-index: 8;
  width: 70%;
  height: auto;
  background-color: #fff;
  top: 10px;
  right: 0;
}

.reportBox h4 {
  padding: 5px;
  line-height: 2rem;
  font-size: 1.1rem;
  font-weight: bold;
  text-align: center;
  font-family: "Noto Sans KR", sans-serif;
}

.reportBox li {
  line-height: 1rem;
  margin-bottom: 13px;
}

.App {
  text-align: center;
  padding-top: 2rem;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
  background-color: #000;
  opacity: 0.5;
}

.modal-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  outline: 0;
}

.modal {
  z-index: 100;
  background: white;
  position: relative;
  margin: 1.75rem auto;
  border-radius: 3px;
  max-width: 500px;
  padding: 2rem;
}

.modal-header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.modal-close-button {
  font-size: 1.4rem;
  font-weight: 700;
  line-height: 1;
  color: #000;
  opacity: 0.3;
  cursor: pointer;
  border: none;
}

button {
  font-size: 0.9rem;
  font-weight: 700;
  border: none;
  border-radius: 3px;
  padding: 0.3rem 1rem;
  margin-left: 0.5rem;
}

.button-default {
  background: #247ba0;
  color: #fff;
}

